.callpageui {
  width: 100%;
  display: flex;
  .shareLink {
    position: absolute;
    top: 40px;
    left: 40px;
    width: 100%;
    max-width: 320px;
    height: 400px;
    background: #fff;
    padding: 20px;

    border-radius: 7px;
    box-shadow: 2px 2px 15px 6px rgba(0, 0, 0, 0.14);
    cursor: pointer;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    color: #5f6368;
    text-align: left;
    transition: all 0.4s ease-in-out;
    &:hover {
      box-shadow: 2px 2px 15px 2px rgb(119, 149, 245);
    }
    @media (max-width: 425px) {
      position: relative;
      top: 0px;
      left: 0px;
    }

    .heading {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      width: 100%;

      .icon {
        transform: rotate(45deg);
        transition: all 0.2s ease-in-out;
        cursor: pointer;
        &:hover {
          transform: rotate(315deg);
          transition: all 0.5s ease-in-out;
        }
      }
    }
    .btn-group {
      .btn-primary {
        background: #1a73e8;
        .icon {
          padding-right: 10px;
        }
      }
    }
    .meeting-links {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: #5f6368;
      padding-left: 10px;

      border-radius: 5px;
      color: #fff;
      .link {
        background: none;
        border: none;
        color: #fff;
        font-size: 20px;
        overflow: hidden;
        width: 66%;
      }

      .icon {
        padding: 20px;
        background: #7e7e7e;
        cursor: pointer;
        color: #ffffff;
        font-size: 25px;
        border-radius: 0 5px 5px 0px;
        &.active {
          color: #1a73e8;
        }
        &:hover {
          background: #999999;
          cursor: pointer;
          color: #145dbd;
        }
      }
    }
    .shild-msg {
      display: flex;
      align-items: center;
      justify-content: space-between;
      &.show {
        display: flex;
      }
      &.hide {
        display: none;
      }
      .icon {
        padding-right: 10px;
        color: #1a73e8;
      }
    }
    .copiedSuccess {
      display: block;
      color: green;
      &.show {
        display: flex;
      }
      &.hide {
        display: none;
      }
    }
  }
}
